.contacts {
  background-image: url("../src/Components/Contacts/style/bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 99vh;
  margin-top: -45.4vh;

  &__content {
    padding: 37vh 10.3vw 0 10.2vw;
    width: -webkit-fill-available;
    justify-content: space-between;

    &__left {
      height: 100%;
      width: 53.8%;
      background-color: rgba(75, 90, 99, 0.71);

      &__container {
        padding: 15px 30px 20px 29px;
        &__row {
          border-bottom: 1px solid $c-gray;
          height: 60px;

          &-big {
            height: 132px;
          }

          &__input {
            background: transparent;
            border: none;
            color: $c-gray;
            font-size: 15px;
            width: 100%;
            margin: 30px 0 0 7px;
            font-family: $ff-desk;

            &::-webkit-input-placeholder {
              color: $c-gray;
              font-family: $ff-desk;
            }

            &-big {
              margin: 27px 0 0 7px;
            }
          }
        }

        &__btn {
          font-family: $ff-montser;
          border: 2px solid black;
          border-radius: 2px;
          font-size: 20px;
          font-weight: 600;
          letter-spacing: 0.5px;
          color: $c-blackOpacity;
          margin: 38px 0 0 2px;
          width: 244px;
          height: 53px;
          display: flex;
          justify-content: center;
          align-items: center;
          transition: 0.5s;
          box-sizing: border-box;
          cursor: pointer;

          &:hover {
            border-color: $c-aqua;
            background-color: $c-aqua;
          }
        }
      }
    }
    font-family: $ff-desk;
    &__right {
      height: 100%;
      width: 44%;
      background-color: rgba(75, 90, 99, 0.71);

      font-size: 15px;
      color: $c-whiteOpacity;

      &__container {
        padding: 15px;

        a {
          color: $c-whiteOpacity;
        }

        &__top {
          padding: 17px;

          &__adress {
            margin-bottom: 7px;
          }

          &__tel {
            margin-bottom: 7px;
          }
        }

        &__middle {
          padding: 5px 0 0 17px;

          &__web {
            margin-bottom: 7px;
          }

          &__mail {
            margin-bottom: 7px;
          }
        }

        &__map {
          padding: 15px 0 0 8px;
        }
      }
    }
  }
}

@media screen and(max-width: 1200px) {
  .contacts {
    height: auto;

    &__content {
      flex-direction: column;
      align-items: center;
      height: auto;

      &__right {
        margin-bottom: 50px;
        margin-top: 50px;
        width: 80%;

        &__container {
          &__map {
            padding: 15px 0 0 0;
          }
        }
      }

      &__left {
        width: 80%;
        &__container {
          &__btn {
            width: 80%;
            margin: 30px auto 0 auto;
            font-size: 90%;
          }
        }
      }
    }
  }
}

@media screen and(max-height: 750px) {
  .contacts {
    height: auto;
    &__content {
      height: 100%;
      padding-bottom: 50px;

      &__left {
        height: auto;
      }
    }
  }
}
